//#region 時間選單連動
const getTimeList = (isLeft, nowTheirSelected) => {
    if (!!nowTheirSelected) {
        if (isLeft) {
            return times.filter((t) => (parseInt(t.value.replace(":", "")) < parseInt(nowTheirSelected.value.replace(":", ""))));
        } else {
            return times.filter((t) => (parseInt(t.value.replace(":", "")) > parseInt(nowTheirSelected.value.replace(":", ""))));
        }
    } else {
        // nowTheirSelected===""
        return times;
    }
}
